@import 'https://fonts.googleapis.com/css?family=PT+Sans:400,700';
body{
  margin:0;
  padding:0;
  width:100%;
  height:100vh;
  font-family:'PT Sans';
  font-size:18px;
  background-color:#006994;
}
.tiles{
  padding:6vh 16vh;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack:center;
      justify-content:center;
  position:relative;
}
.tile{
  margin:15px;
  cursor:pointer;
  overflow:hidden;
  width:18vw;
  height:18vw;
}
.tile img{
  width:100%;
  -webkit-transition:-webkit-transform 500ms ease;
  transition:-webkit-transform 500ms ease;
  -o-transition:transform 500ms ease;
  transition:transform 500ms ease;
  transition:transform 500ms ease, -webkit-transform 500ms ease;
}
.tile img:hover{
  -webkit-transform:scale(1.1);
      -ms-transform:scale(1.1);
          transform:scale(1.1);
}
h1{
  text-align:center;
}
small{
  color:#ccc
}
.small{
  font-size:small;
  color:#ccc;
  text-align:center;
}



